.lume-two-columns {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.lume-two-columns__col--bg {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-wrap: nowrap;
  flex-direction: column;
  overflow: hidden;
  min-height: 600px;
}
.lume-two-columns__col--bg:before {
  content: "";
  position: absolute;
  top: -10px;
  z-index: 2;
  width: 100px;
  height: 102%;
  background-size: auto 100%;
  background-position: center;
  background-repeat: no-repeat;
  transform: scale(-1);
}
.lume-two-columns__col--bg-image {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.lume-two-columns__col--bg.contain .lume-two-columns__col--bg-image {
  object-fit: contain;
}
.lume-two-columns__col--bg .video-wrapper {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.lume-two-columns__col--bg .video-wrapper video {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.lume-two-columns__col--bg .video-wrapper video::-webkit-media-controls-start-playback-button, .lume-two-columns__col--bg .video-wrapper video::-webkit-media-controls-play-button, .lume-two-columns__col--bg .video-wrapper video::-webkit-media-controls-overlay-play-button, .lume-two-columns__col--bg .video-wrapper video::-webkit-media-controls-panel, .lume-two-columns__col--bg .video-wrapper video::-webkit-media-controls {
  display: none;
}
.lume-two-columns.right .lume-two-columns__col--bg:before {
  left: 35px;
  background-image: url("../../assets/images/mask-borderline-left.png");
}
.lume-two-columns.right .mask-wrapper {
  left: 0;
  -webkit-mask-image: url("../../assets/images/mask-image-right.png");
  mask-image: url("../../assets/images/mask-image-right.png");
  mask-position: left;
}
.lume-two-columns.left .lume-two-columns__col--bg:before {
  right: 35px;
  background-image: url("../../assets/images/mask-borderline-right.png");
}
.lume-two-columns.left .mask-wrapper {
  right: 0;
  -webkit-mask-image: url("../../assets/images/mask-image-left.png");
  mask-image: url("../../assets/images/mask-image-left.png");
  mask-position: right;
}
.lume-two-columns .mask-wrapper {
  position: absolute;
  top: 0;
  z-index: 2;
  height: 100%;
  width: 100px;
  background-color: #eee8db;
  mask-repeat: no-repeat;
  mask-size: auto 100%;
  transform: scale(-1);
}
@media screen and (min-width: 992px) {
  .lume-two-columns__col {
    flex: 0 0 50%;
    padding-right: 35px;
    padding-left: 35px;
  }
}
@media screen and (max-width: 1199px) {
  .lume-two-columns.right .mask-wrapper {
    left: -2px;
  }
  .lume-two-columns.left .mask-wrapper {
    right: -2px;
  }
}
@media screen and (max-width: 991px) {
  .lume-two-columns__col--bg:before {
    top: -2px;
  }
}
@media screen and (max-width: 767px) {
  .lume-two-columns.right .lume-two-columns__col--bg:before {
    left: 5px;
  }
  .lume-two-columns.right .mask-wrapper {
    left: -40px;
  }
  .lume-two-columns.left .lume-two-columns__col--bg:before {
    right: 5px;
  }
  .lume-two-columns.left .mask-wrapper {
    right: -40px;
  }
}

body:not(.wp-admin) .lume-two-columns {
  position: relative;
  left: 50%;
  right: 50%;
  display: flex;
  flex-wrap: wrap;
  margin-left: -50vw;
  margin-right: -50vw;
  width: 100vw;
  z-index: 1;
  overflow: hidden;
}
body:not(.wp-admin) .lume-two-columns__col {
  flex: 0 0 100%;
  padding-right: 25px;
  padding-left: 25px;
}
body:not(.wp-admin) .lume-two-columns__col--content {
  align-self: center;
}
body:not(.wp-admin) .lume-two-columns__col--content .acf-innerblocks-container {
  padding-top: 60px;
  padding-bottom: 60px;
  margin: auto;
}
body:not(.wp-admin) .lume-two-columns__col--content .acf-innerblocks-container > *:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 992px) {
  body:not(.wp-admin) .lume-two-columns {
    flex-wrap: nowrap;
  }
  body:not(.wp-admin) .lume-two-columns__col {
    flex: 0 0 50%;
    padding-right: 35px;
    padding-left: 35px;
  }
  body:not(.wp-admin) .lume-two-columns__col--content .acf-innerblocks-container {
    padding-top: 69px;
    padding-bottom: 69px;
  }
}
@media screen and (min-width: 1199px) {
  body:not(.wp-admin) .lume-two-columns__col--bg {
    min-height: 600px;
  }
}
@media screen and (min-width: 1234px) {
  body:not(.wp-admin) .lume-two-columns__col--content.left {
    padding-left: calc((100vw - 1235px) / 2);
  }
}
@media screen and (min-width: 1350px) {
  body:not(.wp-admin) .lume-two-columns__col--content.left .acf-innerblocks-container {
    margin-left: 0;
  }
  body:not(.wp-admin) .lume-two-columns__col--content.right .acf-innerblocks-container {
    margin-right: 0;
  }
}
@media screen and (max-width: 991px) {
  body:not(.wp-admin) .lume-two-columns:not(.reverse-mobile) .right .acf-innerblocks-container {
    padding-top: 30px;
  }
  body:not(.wp-admin) .lume-two-columns.reverse-mobile > .lume-two-columns__col:first-of-type {
    order: 2;
  }
  body:not(.wp-admin) .lume-two-columns__col--bg {
    min-height: 400px !important;
  }
  body:not(.wp-admin) .lume-two-columns__col--bg.contain {
    padding-top: 30px;
    min-height: 0 !important;
  }
  body:not(.wp-admin) .lume-two-columns__col--bg.contain .lume-two-columns__col--bg-image {
    position: relative;
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 325px;
  }
  body:not(.wp-admin) .lume-two-columns__col--content .acf-innerblocks-container {
    max-width: 100%;
  }
}